@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);

.property-block{display:flex;justify-content:center;align-items:center;flex-direction:column}.property-block__title{font-weight:500;font-size:17px}.property-block__subTitle{font-weight:500;font-size:13px;color:gray}

.property-list{display:flex;justify-content:space-between;align-items:center;border-top:1px solid #eee;border-bottom:1px solid #eee;padding:20px 0;margin-bottom:20px}

.display-flex,.button-group__button,.button-group__count{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.button{cursor:pointer;display:flex;justify-content:center;align-items:center;color:black;border-radius:50px;border:2px solid black;font-size:19px;font-weight:500;min-width:86px;height:40px;box-sizing:border-box;outline:none;white-space:nowrap}.button_disabled{color:#c4c4c4;border-color:#c4c4c4}.button-group{display:flex;justify-content:space-between;align-items:center;border-color:#f44336}.button-group__button{color:#f44336;font-weight:500;font-size:26px;padding-left:5px}.button-group__button:last-child{padding:0 5px 0 0}.button-group__count{align-items:center}.extra-product-list__mode-horizontal .button{height:30px}.extra-product-list__mode-horizontal .button-group{border:none}.extra-product-list__mode-horizontal .button-group__button{box-sizing:border-box;padding:0;max-width:30px;border-radius:150px;border:2px solid #f44336}

.extra-product-block{display:flex;justify-content:space-between;align-items:center}.extra-product-block__properties{display:flex;justify-content:center;align-items:center}.extra-product-block__properties img{height:80px;width:80px;object-fit:cover;margin-right:15px}.extra-product-block__properties .properties__title{font-size:15px;font-weight:500}.extra-product-block__properties .properties__subTitle{font-size:14px;color:gray}.extra-product-list__mode-horizontal .extra-product-block{flex-direction:column;margin-right:10px}.extra-product-list__mode-horizontal .extra-product-block:last-child{margin-right:0}.extra-product-list__mode-horizontal .extra-product-block__properties{flex-direction:column}.extra-product-list__mode-horizontal .extra-product-block__properties img{margin:0}

.extra-product-list{border-top:1px solid #eee;padding:20px 0}.extra-product-list__mode-horizontal{height:178px;margin-bottom:0;display:flex;overflow-x:scroll;will-change:transform}

.card-product{padding:0 20px 70px 20px}.card-product-icon img{width:100%;margin-bottom:20px;min-height:50vw;display:block}.card-product-title{display:flex;justify-content:space-between;align-items:center;margin-bottom:20px}.card-product-title span{font-size:19px;font-weight:500}.card-product-description{margin-bottom:20px;font-size:15px;font-weight:400}

.shopping-basket{display:flex;justify-content:center;align-items:center;position:fixed;bottom:20px;right:20px;height:60px;min-width:100px;padding:0 15px;box-sizing:border-box;border:none;border-radius:50px;box-shadow:0 0 16px -10px black;color:white;background-color:#171717}

.navbar{height:48px;display:grid;grid-template-columns:15% 70% 15%;align-items:center}.navbar__left{padding:0;cursor:pointer;border:none}.navbar__left img{height:18px;padding:10px}.navbar__title{margin:0 auto;grid-column-start:2;text-transform:uppercase;font-size:25px;font-weight:500}

.product{cursor:pointer;height:184px;width:100%;display:flex;align-items:center;justify-content:flex-start}.product-icon img{height:180px;width:180px;margin-left:-14px;object-fit:cover}.product-description{margin-left:10px;width:100%;display:flex;justify-content:center;align-items:center;flex-direction:column}.product-description__title{font-size:17px;font-weight:500;text-align:center}.product-description__weight-calorie{font-size:14px;color:#c4c4c4;margin-bottom:20px}.product-price{position:relative;display:flex;justify-content:flex-start;align-items:center;height:100%}.product-price .button{margin-right:5px}.product-price__icon{position:absolute;top:-10px;right:0}

.title-style,.pay-wishes__title,.pay-pay__title{font-size:26px;font-weight:500}.pay{padding:0 20px}.pay-wishes{margin-bottom:40px}.pay-wishes__list-item{margin-top:22px;display:grid;grid-template-columns:18% 47% 35%;align-items:center}.pay-wishes__list-item-icon{max-height:41px;max-width:41px;border-radius:150px;background-color:#eee;box-sizing:border-box;padding:11px;display:flex;justify-content:center;align-items:center}.pay .wishes-item__content-title{font-size:15px;font-weight:500}.pay .wishes-item__content-subtitle{font-size:13px;color:gray}.pay-pay__price-list{display:flex;align-items:center;justify-content:flex-start;margin:12px 0 31px 0}.pay-pay__price-list-item{display:flex;align-items:flex-start;justify-content:center;flex-direction:column;margin-right:20px;font-size:18px}.pay-pay__price-list-item span{font-size:18px;font-weight:500}.pay-pay__price-list-item span:last-child{font-size:13px;font-weight:normal}.pay-pay__price-list-item:first-child{color:#f44336}.pay-pay__price-list-item:last-child{margin-right:0}.pay-pay__button{width:100%;padding:15px 0 18px 0;margin:20px 0 100px 0;box-sizing:border-box;text-align:center;font-size:20px;color:white;background-color:#171717;border-radius:50px;box-shadow:0 0 16px -10px black;cursor:pointer}

.location-button{cursor:pointer;height:100%;display:flex;align-items:center;justify-content:center;flex-direction:column;border:none;border-radius:50px;box-shadow:0 0 16px -10px black;box-sizing:border-box;padding:10px 20px;white-space:nowrap;transition:all 0.3s}.location-button__address{font-size:18px;white-space:nowrap}.location-button__time{font-size:16px;margin-top:3px;color:gray}.location-button_active{background-color:#f44336;color:white;transition:all 0.3s}.location-button_active .location-button__address{color:white}.location-button_active .location-button__time{color:rgba(255,255,255,0.5)}

.location-list{display:flex;overflow-x:scroll;padding:10px 20px;box-sizing:border-box}.location-list .location-button{margin-right:15px}

.product-list{padding:0 20px 80px 20px}

body {
  margin: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 100%;
  margin: 0 auto;
  max-width: 600px;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

